<template>
    <div id="app">
        <div v-if="!loggedIn" class="login-page">
            <div class="login-container">
                <h1 class="app-title">🎵 欢迎来到音乐世界</h1>
                <p class="app-subtitle">一个不一样的登录体验</p>

                <!-- 普通用户名输入 -->
                <div class="form-group" v-if="!showPasswordGame">
                    <label>用户名</label>
                    <input
                        v-model="username"
                        type="text"
                        placeholder="请输入用户名"
                        @keyup.enter="focusPassword"
                        class="normal-input"
                    />
                </div>

                <!-- 密码输入框（点击后触发游戏） -->
                <div class="form-group" v-if="!showPasswordGame">
                    <label>密码</label>
                    <div
                        class="password-input-wrapper"
                        @click="triggerPasswordGame"
                    >
                        <input
                            type="password"
                            placeholder="点击输入密码..."
                            readonly
                            class="password-trigger"
                        />
                        <span class="hint-badge">🎮</span>
                    </div>
                </div>

                <button
                    v-if="!showPasswordGame"
                    class="login-button disabled"
                    disabled
                >
                    请完成密码输入
                </button>
            </div>

            <!-- 节奏密码游戏 -->
            <RhythmPassword
                v-if="showPasswordGame"
                @success="handlePasswordSuccess"
            />
        </div>

        <!-- 登录成功后的界面 -->
        <div v-else class="main-page">
            <div class="welcome-section">
                <h1 class="welcome-title">🎊 欢迎回来！</h1>
                <div class="user-info">
                    <p class="username-display">
                        用户：{{ username || "Guest" }}
                    </p>
                    <div class="stats-summary">
                        <div class="stat-card">
                            <div class="stat-icon">🎯</div>
                            <div class="stat-content">
                                <div class="stat-value">
                                    {{ loginStats.accuracy }}%
                                </div>
                                <div class="stat-label">准确率</div>
                            </div>
                        </div>
                        <div class="stat-card">
                            <div class="stat-icon">🔥</div>
                            <div class="stat-content">
                                <div class="stat-value">
                                    {{ loginStats.combo }}
                                </div>
                                <div class="stat-label">最高连击</div>
                            </div>
                        </div>
                        <div class="stat-card">
                            <div class="stat-icon">🎮</div>
                            <div class="stat-content">
                                <div class="stat-value">
                                    {{ loginStats.attempts }}
                                </div>
                                <div class="stat-label">尝试次数</div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="message-box">
                    <h2>恭喜你完成了这个反人类的登录挑战！</h2>
                    <p>你已经成功证明了自己的节奏感和耐心。</p>
                    <p>现在你可以：</p>
                    <ul>
                        <li>炫耀你的成绩给朋友看</li>
                        <li>再来一次挑战更高分数</li>
                        <li>或者就安静地享受这份成就感</li>
                    </ul>
                </div>

                <div class="action-buttons">
                    <button @click="logout" class="action-button logout-btn">
                        退出登录（重新挑战）
                    </button>
                    <button @click="shareScore" class="action-button share-btn">
                        分享成绩 🎉
                    </button>
                </div>

                <div class="fun-fact">
                    <p>💡 有趣的事实：</p>
                    <p>{{ funFacts[currentFactIndex] }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
import RhythmPassword from "./components/RhythmPassword.vue";

const username = ref("");
const loggedIn = ref(false);
const showPasswordGame = ref(false);
const loginStats = ref({
    accuracy: 0,
    combo: 0,
    attempts: 0,
});

const funFacts = [
    "你知道吗？平均每个人需要尝试3-5次才能成功登录！",
    "这个密码输入框的设计灵感来自于产品经理的邪恶笑容 😈",
    "据统计，有87%的用户在第一次尝试时都会失败",
    "最快通关记录是18秒，你能打破吗？",
    '这个密码框被评为"年度最折磨人的UI设计"第一名',
    "开发这个功能的工程师表示：我也登不进去",
    "有用户反馈说这比Dark Souls还难...",
    "恭喜你！你已经超越了60%的玩家！",
];

const currentFactIndex = ref(0);

onMounted(() => {
    // 随机选择一个有趣的事实
    currentFactIndex.value = Math.floor(Math.random() * funFacts.length);
});

const focusPassword = () => {
    triggerPasswordGame();
};

const triggerPasswordGame = () => {
    if (!username.value.trim()) {
        alert("请先输入用户名哦~");
        return;
    }
    showPasswordGame.value = true;
};

const handlePasswordSuccess = (data) => {
    loginStats.value = data;
    loggedIn.value = true;
    showPasswordGame.value = false;

    // 播放成功音效（如果需要）
    console.log("登录成功！", data);
};

const logout = () => {
    loggedIn.value = false;
    showPasswordGame.value = false;
    loginStats.value = {
        accuracy: 0,
        combo: 0,
        attempts: 0,
    };
    // 随机更换有趣的事实
    currentFactIndex.value = Math.floor(Math.random() * funFacts.length);
};

const shareScore = () => {
    const text = `我在节奏密码挑战中获得了 ${loginStats.value.accuracy}% 的准确率，最高连击 ${loginStats.value.combo}！你能超越我吗？🎮🎵`;

    if (navigator.share) {
        navigator
            .share({
                title: "节奏密码挑战",
                text: text,
                url: window.location.href,
            })
            .catch((err) => console.log("分享取消", err));
    } else {
        // 复制到剪贴板
        navigator.clipboard
            .writeText(text)
            .then(() => {
                alert("成绩已复制到剪贴板！快去分享吧~");
            })
            .catch(() => {
                alert(text);
            });
    }
};
</script>

